@media (min-width: 700px) {
    /*Tablet and desktop styles*/
    .container {
        background-color: #e5edf1;
        padding: 10px;
        height: 100vh;
    }

    .mainContent {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
        color: green;
        border-radius: 5px;
    }

    .mainInfo {
        width: 75%;
    }
}
@media (min-width: 760px) {
    .mainContent {
        -ms-flex-direction: row;
            flex-direction: row;
    }
}


